import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

function Scrolltop() {
    const location = useLocation();

    useEffect(() => {

        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'instant' //  auto instant  
        });
    }, [location.pathname]); //     
}

export default Scrolltop