import React from "react"
import { Link } from "react-router-dom";

function List_box() {
    return (
        <>
            <div className='site d-flex justify-content-center align-items-center'>
                <ul className="p-0">
                    <li className="m-1 m-md-1 m-lg-2 mt-5">
                        <Link to="/new-books">
                            <img src='./img/icon1.png' className="img-fluid" alt='' />
                            <span></span>
                        </Link>
                    </li>

                    <li className="m-1 m-md-1 m-lg-2 mt-5">
                        <Link to="Bestbook">
                            <img src='./img/icon2.png' className="img-fluid" alt=''></img>
                            <span></span>
                        </Link>
                    </li>

                    <li className="m-1 m-md-1 m-lg-2 mt-5">
                        <Link to="Curation">
                            <img src='./img/icon3.png' className="img-fluid" alt=''></img>
                            <span></span>
                        </Link>
                    </li>

                    <li className="m-1 m-md-1 m-lg-2 mt-5">
                        <Link to="Goods">
                            <img src='./img/icon4.png' className="img-fluid" alt=''></img>
                            <span></span>
                        </Link >
                    </li>
                    <li className="m-1 m-md-1 m-lg-2 mt-5">
                        <Link to="Program">
                            <img src='./img/icon5.png' className="img-fluid" alt=''></img>
                            <span></span>
                        </Link>
                    </li>

                </ul>

            </div>
        </>
    )
}

export default List_box