import axios from 'axios';
export const productApi = async (tablenminfo, data = null) => {

    // const crudinfoarr = tablenminfo.split('/');
    // const tablenm = crudinfoarr[0]; //
    // const pk = crudinfoarr[1] ? crudinfoarr[1] : null; // pk
    // const crud = crudinfoarr[2] ? crudinfoarr[2] : null; //






    //  /api//insert select curd 
    /*   
       1.  post productApi(, )
       2.  get  productApi()  
       3.  get  productApi(/id)   
       4.  post productApi(/id/m, ) 
       5.  post productApi(/id/d) 
    */
    try {

        const [, pk = null, crud = null] = tablenminfo.split('/');
        console.log('Primary key:', pk);

        if (data) {
            //     data  

            const response = await axios.post(`/api/${tablenminfo}`, {
                headers: { "Content-Type": "multipart/form-data" },
                body: data
            });

            console.log(" api ", response); // chat!! here this is full data
            return response;
            // check ok
        }

        else {
            //, 
            if (crud === "d") {
                //  /id/crud
                return await axios.post(`/api/${tablenminfo}`,
                    { headers: { "Content-Type": "multipart/form-data", } });
            } else {
                // 
                return await axios.get(`/api/${tablenminfo}`);
            }



        }

    } catch (error) {
        console.error("  :", error);
        return error;

    }

}