import React from "react";
import axios from "axios";
import { useForm } from "react-hook-form";
import { useState } from "react";

function Form() {

    // const productApi = async (myName, data = null) => {
    //  myname sql , get post 
    //     try {
    //         if (data) { //

    //             const response = await axios.post(`/data/${myName}`, {
    //                 headers: {
    //                     'Content-Type': 'multipart/form-data',
    //                 },
    //                 body: data // form  
    //             });

    //             if (!response.ok) {
    //                 throw new Error('Network response was not ok');
    //             }

    //             const responseData = await response.json(); // Assuming the response is JSON, adjust as necessary
    //             return responseData;
    //         } else { // 
    //             return axios.get(`/data/${myName}`);
    //         }

    //     } catch (error) {
    //         console.log(error);
    //         return error;
    //     }
    // };
    const [formdata, setformdata] = useState({ //input name 
        name: '',
        address: '',
        email: '',
    })
    const handleChange = (e) => {
        // 
        const { name, value } = e.target;
        setformdata(prevState => ({
            ...prevState,
            [name]: value
        }));
    };


    const {
        register,
        handleSubmit,
        formState: { isSubmitting, isSubmitted, errors },
    } = useForm({ mode: 'onChange' });

    const onSubmit = async (data) => {
        alert(JSON.stringify(data))
        console.log(data);
    };


    return (
        <div className="sectionform">
            <form className="form " noValidate onSubmit={handleSubmit(onSubmit)}>
                <div className=" d-flex justify-content-center ">
                    <h1 className="formh mt-3 sm-d-flex "></h1>
                </div>

                <fieldset>
                    <div class="userin d-flex justify-content-center align-items-center py-3">
                        <div class="dda row justify-content-around align-items-center container pt-5 px-5 ">
                            <div className="user mx-auto col-sm-5 text-start d-flex ">
                                <div className="  mx-auto text-start ">
                                    <div className="bor mx-auto text-start">
                                        <label className="" htmlFor="name">

                                        </label>
                                        <input
                                            className={`mb-4 0 ${errors.name ? "invalid" : "valid"}`}
                                            id="name"
                                            type="text"
                                            name="name"
                                            placeholder=""
                                            value={formdata.name}
                                            {...register("name", { required: "  ." })}
                                        />
                                        {errors.name && <small>{errors.name.message}</small>}
                                        <div >
                                            <label className="" htmlFor="address">

                                            </label>
                                            <input
                                                className={` mb-4 0 ${errors.address ? "invalid" : "valid"}`}
                                                id="address"
                                                name="address"
                                                type="text"
                                                placeholder=""
                                                {...register("address", { required: "  ." })}
                                            />
                                            {errors.address && <small>{errors.address.message}</small>}
                                        </div>
                                        <div>
                                            <label className="" htmlFor="email">
                                            </label>
                                            <input
                                                className={`mb-4 0 ${errors.email ? "invalid" : "valid"}`}
                                                id="email"
                                                type="email"
                                                name="email"
                                                placeholder="test@email.com"
                                                {...register("email", {
                                                    required: "  .",
                                                    pattern: {
                                                        value: /\S+@\S+\.\S+/,
                                                        message: "   .",
                                                    },
                                                })}
                                                aria-invalid={isSubmitted ? (errors.email ? "true" : "false") : undefined
                                                }
                                                style={{ borderBottom: '1px solid white' }}
                                            />

                                            {errors.email && (
                                                <small role="alert" id="email-error">
                                                    {errors.email.message}
                                                </small>
                                            )}
                                        </div>
                                    </div>

                                </div>
                            </div>
                            <div className="tarea col-5 ms-auto col-sm-d-block">
                                <textarea
                                    className="text  position-rel "
                                    id="tx"
                                    cols="30"
                                    rows="3"
                                    placeholder="     
                                   ."
                                    {...register("message")}
                                ></textarea>
                            </div>
                        </div>
                    </div>
                    <div className="btns">
                        <button type="submit" disabled={isSubmitting}>
                            
                        </button>
                    </div>
                </fieldset>
            </form>
        </div>
    );
}

export default Form;


